<template>
  <div class="passinfo-container">
    <div class="passinfo-box">
      <NavBar />
      <span class="passinfo-box--title">输入验证码</span>
    </div>
    <p class="yzm-font">已发送6位数验证码至</p>
    <p class="photo-num">+86{{ merchantContactPhone }}</p>
    <van-password-input
      class="input-layout"
      :value="yzm"
      :focused="showKeyboard"
      @focus="showKeyboard = true"
    />
    <van-number-keyboard
      class="keyboard passinfo-box--keyboard theme-keyboard-color"
      :show="showKeyboard"
      safe-area-inset-bottom
      theme="custom"
      extra-key="."
      close-button-text="完成"
      @input="onInput"
      @delete="onDelete"
      @close="showKeyboard = false"
    />
    <van-goods-action>
      <van-button
        @click="getMoney"
        :class="[showKeyboard ? 'passinfo-up' : 'passinfo-down']"
        size="large"
        >立即提现</van-button
      >
    </van-goods-action>
  </div>
</template>
<script>
import { PasswordInput, NumberKeyboard, Panel, Button, GoodsAction, Toast } from 'vant'
import { toWithdraw, getAccountInfo } from '../../api/index'
import NavBar from '../navbar'

export default {
  name: 'passinfo',
  components: {
    NavBar,
    [PasswordInput.name]: PasswordInput,
    [NumberKeyboard.name]: NumberKeyboard,
    [Panel.name]: Panel,
    [Button.name]: Button,
    [GoodsAction.name]: GoodsAction
  },
  data() {
    return {
      value: '123',
      showKeyboard: true,
      yzm: '', //验证码
      code: this.$route.params.anCode || '', //商编
      cardNo: this.$route.query.cardindex || 0, //卡列表中的index
      dollor: this.$route.query.dollor || 0, //提现金额
      bankName: this.$route.query.bank, //银行名称
      bindcard: this.$route.query.bindcard, //绑定银行卡
      payAccountName: this.$route.query.payacc, //支付账户
      accountInfo: {}, //get返回信息
      drawParams: {}, //普通提现--企业付款--提现函数入参
      merchantContactPhone: '', //电话号码
      funres: '' //返回结果
    }
  },
  created() {
    this.refer()
  },
  methods: {
    //main get 接口的调用
    async refer() {
      let res = await getAccountInfo(this.code)
      this.accountInfo = { ...res.data }
      // console.log(this.accountInfo,'accountinfo')
      this.merchantContactPhone = this.accountInfo.merchantContactPhone
    },
    //立即提现接口调用
    async nowWithdraw() {
      this.drawParams.wuuxiangAccount = this.code
      this.drawParams.companyPaymentState =
        this.accountInfo.companyPaymentState == 'SUCCESS' ? '1' : '0'
      this.drawParams.merchantContactPhone = this.accountInfo.merchantContactPhone
      this.drawParams.authCode = this.yzm
      this.drawParams.settleAmount = this.dollor
      // 如果是企业付款提现
      if (this.accountInfo.companyPaymentState === 'SUCCESS') {
        this.accountInfo.privateNolegalpersonAccount.map(item => {
          if (item.accountNo == this.bindcard) {
            this.drawParams.bindCardNo = item.bindCardNo
          }
        })
      }
      this.drawParams.bankName = this.bankName
      this.drawParams.payAccountName = this.payAccountName
      let func = await toWithdraw(this.drawParams)
      this.funres = { ...func }
      console.log(this.funres.code, 'this.funres')
    },
    onInput(key) {
      this.yzm = (this.yzm + key).slice(0, 6)
    },
    onDelete() {
      this.yzm = this.yzm.slice(0, this.yzm.length - 1)
    },
    async getMoney() {
      Toast.setDefaultOptions({ duration: 500 })
      Toast('为了演示方便，去掉了校验')
      this.$router.push('/resultsteps')
      // if (this.yzm == '') {
      //   Toast.fail('请输入验证码')
      // } else if (this.yzm.length < 6) {
      //   Toast.fail('验证码不完整')
      // } else {
      //   await this.nowWithdraw()
      //   if (this.funres.code !== '0') {
      //     Toast.fail(this.funres.msg)
      //   } else {
      //     this.$router.push('/resultsteps')
      //   }
      // }
    }
  }
}
</script>
<style lang="less">
.passinfo-box {
  background-size: 100%;
  width: 100%;
  font-size: @base-size * 1.4;
  box-sizing: border-box;
  background: url(../../assets/images/ssg/pass_word.png) no-repeat top;
  padding-bottom: @base-size*2;
  &--title {
    font-size: @base-size*2.8;
    color: @ft-white;
    margin: 20px 0px 10px 20px;
  }
  &--keyboard {
    color: @ft-black;
    .van-key--blue,
    .van-key--blue.van-key--active {
      background-color: @btn-color-ssg;
      &:active {
        opacity: 0.9;
      }
      &::after {
        border-color: @btn-color-ssg;
      }
    }
  }

  .yzm-font {
    font-size: @base-size*2;
    font-weight: bold;
    margin-left: @base-size*2;
  }
  .photo-num {
    font-size: @base-size*3;
    font-weight: bold;
    margin-left: @base-size*2;
  }
  .input-layout {
    margin: @base-size*4 auto;
  }
}

.passinfo-container {
  .passinfo-down {
    width: 90%;
    display: block;
    margin: @base-size*2 auto;
    font-size: @base-size*1.8;
    color: @ft-color-ssg;
    background-color: @btn-color-ssg;
    border-radius: @base-size*5;
    font-weight: bold;
  }
  .passinfo-up {
    width: 90%;
    display: block;
    font-size: @base-size*1.8;
    color: @ft-color-ssg;
    margin: 0 auto;
    background-color: @btn-color-ssg;
    border-radius: @base-size*5;
    transform: translateY(-230px);
    font-weight: bold;
  }
}
</style>
